@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
.body {
   margin: 0;
   font-family: 'Montserrat', sans-serif;
   width: 100%;
}

p, blockquote {
    font-size: .9em;
    line-height: 1.6em;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.container {
    text-align: center;
    padding: .8em 1.2em;
}

.blue-container{
    width: 100%;
    background: #0B8BEC;
    color: #fff;
    margin-top: 15%;
    padding: 4em 0 2em;
}

.blue-container ul li {
    margin-bottom:  3em;
}

.blue-container ul li p {
    padding: 0 .5em;
}

.blue-container img {
    width: 14%;
}

.demoContainer {
    text-align: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.demoContainer img {
    width: 100%;
    height: auto;
    margin-bottom: 8%;
}

h2 {
    margin: 7% auto 7%;
}

.hide-desktop {
    cursor: pointer;
}
.gray-container {
    background: #F6F6F6;
}
.cta2 {
    background: #0793FF;
    width: calc(100% - 1em);
    display: block;
    color: #fff;
    padding: .5em;
    text-decoration: none;
    font-size: 1.5em;
    margin: 3% auto 7%;
}

.footer-container {
    width: 100%;
    background: #3E454B;
    color: #fff;
    padding: 3em 0;
}


ul.footer-links{
    margin: 0 auto;
}

ul.footer-links li{
    display: inline block;

}

ul.footer-links li a{
    color: #C1C1C1;
    font-size: .8em;    
}

.heroimg {
    width: 70%;
    margin: 3em auto;
}

.subhead {
    font-size: 1.1em;
    margin-bottom: 17%;
}

.logo {
    width: 130px;
}

.hide-mobile{
    display: none;
}

.header {
    display: flex;
    justify-content: space-between;
}

.menu {
    width: 25px;
    margin-top: 88%;
}

#icc11 {
   width: 18%;
}

.header nav ul {
    position: fixed;
    width: 60%;
    top: 0;
    right: 0;
    text-align: left;
    background: rgb(52, 59, 63);
    height: 100%;
    z-index: 7;
    padding-top: 3em;
}

.header nav ul li a {
    color: white;
    text-decoration: none;
    width: 100%;
    display: block;
    padding: 1em 2em;
    background-color: rgb(64, 67, 70);
}

.imgcont ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;

}

.imgcont img {
    max-width: 100%;
}

 nav ul li a:hover {
    background-color:  rgb(92, 96, 100);
}

.exit-btn {
    margin-bottom: 1em;
    margin-top: -1.3em;
    text-align: right;
    padding: 0 1.4em;
}



.exit-btn img {
    width: 15px;
    cursor: pointer;
}

.container button {
    background-color: #0793FF;
    color: #fff;
    border: none;
    border-radius: 24px;
    width: 200px;
    height: 50px;
    font-size: 22px;
    font-weight: bold;
    cursor: pointer;
}


.gray-container h2 {
    font-weight: 500;
}

.gray-container {
    font-weight: 700;
}

@media only screen and (min-width: 650px) {

    .heroimg {
        width: 50%;
    }

    .container button {
        margin-bottom: 1.3em;
    }

    h1 {
        font-size: 2.5em;
        margin: 0;
    }

    .subhead {
        font-size: 1.4em;
        margin-bottom: 4%;
    }
  

    .blue-container {
        margin: 0;
    }

    .blue-container img {
        width: 30%;
    }

    .blue-container ul {
        display: flex;
        margin-top: -1em;
        align-items: center;
    }

    .blue-container ul li {
        margin-bottom: 0;
    }

    .blue-container ul li p {
        padding: 0 .8em;
    }

    .imgcont ul {
        display: flex;
    }

    .header nav ul {
        width: 40%;
    }

}


@media only screen and (min-width: 875px) {

    .subhead {
        margin-bottom: 4%;
    }

    

    .container button {
        margin-bottom: 1.9em;
    }

    .blue-container ul {
        margin: 0 0 4% 0;
    }

    .gray-container ul {
        margin: 4% 0 0;
    }
}

@media only screen and (min-width: 1024px) {

    .container {
        width: 80%;
        margin: 0 auto;
    }

    .heroimg {
        width: 45%;
    }

    .show-desktop {
        display: block;
        margin: 0 auto 13% auto;
    }

    .hide-desktop {
        display: none;
    }

    .header nav ul {
        position: inherit;
        width: auto;
        background: none;
        height: auto;
        display: flex;
        padding-top: 0;
    }

    .header nav ul li {
        float: left;
    }

    .header nav ul li a {
        color: #0078D4;
        background-color: inherit;
        text-align: center;
        padding: 1em 2em;
    }

    header.nav ul li a:hover {
        background-color: inherit;
    }

    .demoContainer h2{
        margin: 1.5em auto 1.5em;
        font-size: 2.1em;
    }

    .footer-links {
        display: flex;
        justify-content: space-evenly;
        font-size: 1.1em;
        padding-bottom: 0.5em;
    }

    footer img {
        display: block;
        padding-bottom: 0.2%;
    }

    .footer-container p {
        display: block;
        text-align: start;
       
    }

    .footer-container {
        padding: 1em ;
    }

    footer hr {
        display: block;
        height: 1px;
        border: 0;
        border-top: 1px solid #9A9ACE;
        margin: 1em 0;
        padding: 0;
    }

    .cta2 {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50%;
        height: 45%;
        font-size: 1.4em;
        border-radius: 2em;
    }

    .democta {
        display: grid;
        grid-template-columns: 60% 40%;
        align-items: center;
        justify-content: center;
    }

    .democta h2 {
        font-size: 2em;
    }

    
    .blue-container img {
        width: 20%;
        padding-bottom: 1em;
    }
    
    .blue-container li {
        padding-left: 1em;
        padding-right: 1em;
    }



}

@media only screen and (min-width: 1600px) {

    .heroimg{
        width: 40%;
    }

    .container {
        width: 70%;
    }

    .blue-container {
        padding: 7em 0 2em;
    }

    .blue-container ul li p {
        font-size: 1.3em;
    }

    .blue-container img {
        width: 20%;
    }

    



}


.demo-body {  
  margin: 0;
  padding: 0;    
  font-family: 'Montserrat', sans-serif;
  background-color: rgba(225, 223, 221, 0.2);
  color: #323130;
  overflow: hidden;
}

.demo-header {
  padding: 0.4em 0em 1em 1.5em;
  background-color: #fff;
}
.demo-header img {
 width: 12em;
}


.homepage-demo{
  padding-right: 1.5em;
  padding-top: 1.3em;
  float: right;
  font-size: 15px;
  font-weight: 500;

}

.ms-Nav {
  background-color: white;
 }
 
 .ms-Grid-col {
  padding-left: 0;
  margin-left: 0;
 }


.demo-container {
  display: grid;
  grid-template-columns: 15% auto;
}



@media only screen and (max-width: 1500px) {
  .ms-Nav {
    width: 66%;
  }

  
}



@media only screen and (max-width: 768px) {
    .ms-Nav {
      display: none;
    }
    

}


.dashBody {
    margin: 1em 4em auto auto;
    padding: 0;    
    font-family: 'Montserrat', sans-serif;
    display: grid;
    grid-template-columns: 65% 35%;
    grid-template-rows: auto;
    padding: 1em;
    grid-template-areas:
        "search search"
        "title title"
        "section1 section2";
    grid-gap: 2em;
}

.search1 {
    grid-area: search;    
    margin: 0 auto;
    text-align: center;
}

#s1 {
    grid-area: section1;  
    margin-bottom: 2em;
}

.title {
    grid-area: title;
}
#s2 {
    grid-area: section2;
}

.search1 p {
    margin-bottom: -0.1em;
    font-size: 12px;
}

.autocomplete {
    background-color: white;
    position:absolute;
    z-index: 9;
    cursor: pointer;
    width: auto;

}

.autocomplete li {
    padding-right: 0.5em;
    border-bottom: 1px solid #eee;
    border-left: 1px solid #eee;
}

.autocomplete li:hover {
    background-color: #e1dfdd;
}

h1 {
    padding-top: 0.5em;
    text-align: center;
    padding-bottom: 0.5em;
}

.chart-container {
    width: 100%;
    background-color: white;
}

.search1{
    width: 100vh;
}

#s2 table {
    font-size: 11px;
    background-color: white;
    padding: -1px auto -2px auto;
}

td span{
    float:right;
}
td span:first-child{ /* compatible to >=IE7 */
    float:left;
}

td:hover {
    background-color: #e1dfdd;
}

#s2 h2 {
      text-align: center;
      margin: 0 auto;
      font-size: 28px;
  }

#s2 {
    padding-left: 0;
}

.table1cont {
    padding-bottom: 2em;
}

.positive {
    color: green;
}

.negative {
    color: red;
}

.sentiment-container {
    padding-top: 2em;
}

.sentiment-inside {
    margin: auto;
    display: flex;
    justify-content: center;
}

.sentiment-inside td:hover {
    background-color: #fff;
}

.ms-FocusZone {
    height: 100%;
}

nav {
    height: 100%;
}

.tablea3 {
    margin-top: 2em;
    margin-bottom: 2em;
}

.tableDesc {
    border-top: 1px solid #dbdbd9;
    border-bottom: 1px solid #dbdbd9;
}

.tablesent-cont {
    display: flex;
    justify-content: center;
    align-items: center;
}


.tablesent1 span {
    padding-right: 1em;
}

.tablesent1 span {
    vertical-align: bottom;
}



